* {
  padding: 0;
  margin: 0;
  font-family: 'Times New Roman', Times, serif;
}
.App {
  text-align: center;
}

.App-logo {
  /* height: 40vmin; */
  pointer-events: none;
}
/* div:hover .App-logo {
  transform: scale(1.5);
  background-color: red;
} */
/* .logoImgContainer {
  margin: 0;
} */
@media (prefers-reduced-motion: no-preference) {
  .logoImgContainer:hover .App-logo {
    animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(-360deg);
  }
}

.span_link {
  float: right;
  line-height: 60px;
  margin-right: 3px;
}

